  },
  created() {
    if (this.$route.params.id !== undefined && this.$route.params.attempt) {
      sessionStorage.setItem('id_group', this.$route.params.id);
      sessionStorage.setItem('test_attempt', this.$route.params.attempt);
    }
    this.id = sessionStorage.getItem('id_group');
    this.attempt = parseInt(sessionStorage.getItem('test_attempt'));
    this.getRequest('put');
  },
  methods: {
    getRequest(type = null) {
      $.ajax({
        url: this.$store.state.baseUrl + "api/typologicalGroup/",
        type: "GET",
        data: {group: this.id},
        success: (response) => {
          if (response === undefined) {
            this.no_data = true;
            this.newGroup();
          } else {
            // this.id = response.data[0].id;
            this.items = response.data[0].attributes;
            this.test_id = response.data[0].relationships.test.data.id;
            if (type === 'put') this.update('put');
            else {
              if (this.items.group.indexOf(' 1') + 1) {
                this.text = this.texts[0];
                this.header = this.items.group;
              } else if (this.items.group.indexOf(' 2.1') + 1) {
                this.text = this.texts[1];
                this.header = this.items.group;
              } else if (this.items.group.indexOf(' 2.2') + 1) {
                this.text = this.texts[2];
                this.header = this.items.group;
              } else if (this.items.group === '         ') {
                this.header = this.items.group;
                this.text = '';
              } else {
                this.text = '';
                this.header = '';
              }
            }
          }
        },
        error: (response) => {
          if (response.status === 401) this.logOut();
          else alert("     .\n  .")
        }
      });
    },
    postRequest() {
      $.ajax({
        url: this.$store.state.baseUrl + "api/typologicalGroup/",
        type: "POST",
        data: this.array,
        success: (response) => {
          this.no_data = false;
          this.id = response.data.id;
          sessionStorage.setItem('id_group', this.id);
          this.getRequest();
        },
        error: (response) => {
          alert("     .\n  .")
        }
      });
    },
    putRequest() {
      $.ajax({
        url: this.$store.state.baseUrl + "api/typologicalGroup/" + this.id,
        type: "PUT",
        data: this.array,
        success: (response) => {
          this.no_data = false;
          this.getRequest();
        },
        error: (response) => {
          alert("     .\n  .")
        }
      });
    },
    update(type) {
      $.ajax({
        url: this.$store.state.baseUrl + "api/fieldsGroup/",
        type: "GET",
        data: {client: sessionStorage.getItem('id'), test: this.test_id},
        success: (response) => {
          if (response.data.social_behavior[0] && response.data.boyko[0] && response.data.chronic_disease[0]) {
            if (response.data.social_behavior[0].caseExaminedInKDN_ZP === 1 && response.data.boyko[0].aggressiveness === 1)
              this.array['group'] = 1;
            else if (response.data.social_behavior[0].caseExaminedInKDN_ZP === 2 && response.data.boyko[0].aggressiveness === 0 && response.data.chronic_disease[0].HIVStatus === 2 && response.data.general_info[0].age >= 18 && response.data.general_info[0].age <= 35)
              this.array['group'] = 2;
            else if (response.data.social_behavior[0].caseExaminedInKDN_ZP === 2 && response.data.boyko[0].aggressiveness === 0 && (response.data.chronic_disease[0].hepatitisC === 1 || response.data.chronic_disease[0].HIVStatus === 1) && response.data.general_info[0].age >= 36 && response.data.general_info[0].age <= 60)
              this.array['group'] = 3;
            else
              this.array['group'] = 0;
            if (type === 'put')
              this.putRequest();
            else {
              this.array['client'] = parseInt(sessionStorage.getItem('id'));
              this.array['test'] = parseInt(this.test_id);
              this.postRequest();
            }
          } else {
            if (type === 'put') {
              $.ajax({
                url: this.$store.state.baseUrl + "api/typologicalGroup/" + this.id,
                type: "DELETE",
                success: (response) => {
                },
                error: (response) => {
                  alert("     .\n  .")
                }
              });
            }
            this.no_data = true;
            alert("   ,    , : '     ( ,-)'," +
              " '   , ,  ,  (     )'.")
          }
        },
        error: (response) => {
          alert("     .\n  .")
        }
      });
    },
    newGroup() {
      $.ajax({
        url: this.$store.state.baseUrl + "api/testBoykoList/",
        type: "GET",
        data: {client: sessionStorage.getItem("id")},
        success: (response) => {
          this.test_id = '';
          for (let test in response.data) {
            if (response.data[test].attributes.attempt === this.attempt) {
              this.test_id = response.data[test].id;
              this.update('post')
            }
          }
          if (this.test_id === '') {
            // this.no_data = true;
            alert("    .");
          }
        },
        error: (response) => {
          alert("     .\n  .")
        }
      });
